@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700&family=Roboto:wght@400;500;700;900&display=swap');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
    list-style: none;
}

#header {
    margin: 0 5rem;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
}

#header nav {
    display: flex;
}

#header nav p,
#header nav input {
    margin: 0 1.2rem;
}

#header nav p {
    font-size: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

#header nav input {
    padding: 5px 7.5px;
    border: 1px solid #000;
    border-radius: 4px;
}

.heartIcon,
.bagIcon {
    margin-right: 0.2rem;
}

.heartIcon {
    color: rgb(255, 125, 80);
}

.header__cart p {
    color: #fff;
    background-color: rgb(255, 125, 80);
    border-radius: 100%;
    width: 1.5rem;
    height: 1.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 0.25rem !important;
    margin-right: 0 !important;
}


/*!  section  */

.categories {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin: 1.5vh auto 0;
    width: 77.5%;
}

.categories p {
    cursor: pointer;
    font-weight: 500;
    font-size: 1.25rem;
    text-transform: uppercase;
    font-variant: unicase;
}

.company__logo {
    display: flex;
    margin: 5vh auto 0;
    filter: opacity(0.45);
    transform: scale(1);
}

.cosmetic_one {
    background-image: url('/public/cosmetic1.jpg');
    background-repeat: no-repeat;
    background-size: contain;
    height: 65vh;
    width: auto;
    margin: 0 auto;
    background-position: center;
}


/* ! products section */

.products {
    background-color: #F7F1EA;
    margin-top: 10vh;
    padding-bottom: 10vh;
}

.products__heading {
    padding-top: 10vh;
    font-size: 1.75rem;
    letter-spacing: 0.25px;
    word-spacing: 1.5px;
    font-weight: 500;
    text-transform: uppercase;
    margin-left: 10%;
}

.products__section {
    width: 85%;
    display: flex;
    justify-content: center;
    align-items: baseline;
    flex-wrap: wrap;
    flex-direction: row;
    margin: 7.5vh auto 0;
}

.product__showcase {
    width: 20%;
    display: flex;
    justify-content: center;
    align-items: normal;
    flex-direction: column;
    margin: 0 2.5%;
}

.product-image {
    height: 100%;
    width: auto;
    object-fit: cover;
}

.product-name {
    margin-top: 1.25rem;
    font-weight: 500;
    width: 80%;
    font-size: 1.15rem;
}

.product-price {
    font-family: 'Lato', sans-serif;
    margin-top: 1.25rem;
    font-weight: 600;
    font-size: 1.4rem;
}


/*!  FOOTER  */

.footer_categories {
    margin: 5vh auto;
    width: 80%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 4vh;
    border-bottom: 2px solid rgb(140, 140, 140);
}

.footer_categories p {
    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: 0.5px;
    cursor: pointer;
    font-size: 1.25rem;
}

.footer_categories p span {
    font-weight: 400;
}

.newsletter__section {
    margin: 0 auto;
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    flex-direction: row;
    text-align: center;
}

.newsletter__section h3 {
    font-weight: normal;
    font-size: 1.075rem;
}

.newsletter-info {
    margin-top: 2.25rem;
    font-size: 0.9rem;
    font-weight: 500;
}

.newsletter__section button {
    background: transparent;
    border: 1px solid #000;
    padding: 10px 50px;
    font-size: 1.05rem;
    margin: 1.25rem 0;
    cursor: pointer;
    transition: .275s ease;
}

.newsletter__section button:hover {
    color: #fff;
    background-color: #000;
}